.container-rubber {
  width: 100%; }

.container-standart {
  width: 1280px;
  margin: 0 auto; }

.col-big-3, .col-big-4, .col-big-6, .col-big-9, .col-big-12 {
  float: left; }

.col-big-12 {
  width: 100%; }

.col-big-9 {
  width: 75%; }

.col-big-6 {
  width: 50%; }

.col-big-4 {
  width: 33.33333333%; }

.col-big-3 {
  width: 25%; }

.col-big-1 {
  width: 8.33333333%; }

html, body {
  width: 100%;
  height: 100%; }

body {
  background-color: #fff;
  font-family: 'Roboto', sans-serif;
  font-weight: 400; }

.admin-aside {
  width: 200px;
  height: 100px;
  min-height: 100%;
  background-color: #fff;
  position: fixed; }
  .admin-aside:before {
    content: '';
    display: block;
    position: absolute;
    width: 2px;
    height: 80%;
    background-color: #BCBCBC;
    right: -4px;
    top: 5%;
    transition: all 0.6s; }
  .admin-aside:after {
    content: '';
    display: block;
    position: absolute;
    width: 2px;
    height: 80%;
    background-color: #BCBCBC;
    right: 0;
    top: 15%;
    transition: all 0.6s; }
  .admin-aside:hover:before {
    top: 0%;
    height: 100%;
    background-color: #000; }
  .admin-aside:hover:after {
    top: 0%;
    height: 100%;
    background-color: #000; }
  .admin-aside:hover a {
    color: #000; }

.admin-main {
  width: calc(100% - 200px);
  min-height: 100%;
  float: right;
  box-sizing: border-box;
  padding: 60px 30px 60px 40px;
  position: relative; }
  .admin-main:before {
    content: '';
    display: block;
    width: calc(100% - 204px);
    height: 60px;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 10;
    background: -moz-linear-gradient(top, white 0%, transparent 100%);
    background: -webkit-linear-gradient(top, white 0%, transparent 100%);
    background: linear-gradient(to bottom, white 0%, transparent 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00000000',GradientType=0 ); }
  .admin-main:after {
    content: '';
    display: block;
    width: calc(100% - 204px);
    height: 60px;
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 10;
    background: -moz-linear-gradient(top, transparent 0%, rgba(255, 255, 255, 0.99) 99%, white 100%);
    background: -webkit-linear-gradient(top, transparent 0%, rgba(255, 255, 255, 0.99) 99%, white 100%);
    background: linear-gradient(to bottom, transparent 0%, rgba(255, 255, 255, 0.99) 99%, white 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#ffffff',GradientType=0 ); }

.admin-menu {
  color: #000;
  list-style: none;
  padding: 10px 0px 10px 0px; }
  .admin-menu > * {
    overflow: hidden;
    transition: background 0.6s; }
    .admin-menu > * a {
      display: inline-block;
      width: 100%;
      height: 100%;
      padding: 20px 20px;
      color: #BCBCBC;
      text-decoration: none;
      transition: color 0.6s; }
    .admin-menu > *:hover {
      background-color: #000; }
      .admin-menu > *:hover a {
        color: #fff; }

.table-articles {
  border: 2px solid #666;
  border-collapse: collapse;
  width: 100%; }
  .table-articles td, .table-articles th {
    border: 2px solid #666;
    padding: 10px; }
